import React, { Component } from 'react'
import CalendarView from '../components/Calendar/CalendarView'
import CalendarAddEvent from '../components/Calendar/CalendarAddEvent'
import './calendar.css'

class Calender extends Component {
  constructor() {
    super()
    this.state = {
      popUpEventInfo: {},
      popupPosition: {
        left: 0,
        top: 0
      },
      popUpShown: false
    }

    this.togglePopup = this.togglePopup.bind(this)
  }
  static async getInitialProps() {
    const res = await fetch(
      'https://hpvtexas.wpengine.com/wp-json/better-rest-endpoints/v1/event'
    )
    const data = await res.json()
    return { events: data }
  }
  togglePopup(e, event) {
    var wrap = document.querySelector('.calendar-wrap')
    //e is dom event
    var elPos = e.currentTarget.parentElement.getBoundingClientRect()
    var wrapPos = wrap.getBoundingClientRect()
    console.log(wrapPos)
    console.log('toggle popup')
    this.setState({
      popUpEventInfo: event,
      popupPosition: {
        left: elPos.left + 'px',
        top: elPos.top - wrapPos.top + 'px'
      },
      popUpShown: true
    })

    function closePopup() {
      this.setState({
        popUpShown: false
      })
      window.removeEventListener('click', function(e) {
        closePopup()
      })
    }
    window.addEventListener('click', function(e) {
      closePopup()
    })
  }
  render() {
    return (
      <div>
        <div className="calendar-container">
          {this.state.popUpShown ? (
            <div
              className="calendar-popup"
              style={{
                position: 'absolute',
                left: `${this.state.popupPosition.left}`,
                top: `${this.state.popupPosition.top}`
              }}
            >
              <h2>{this.state.popUpEventInfo.title}</h2>
            </div>
          ) : (
            ''
          )}
          <div className="calendar-left">
            <CalendarAddEvent {...this.props} />
          </div>
          <CalendarView
            events={this.props.events}
            {...this.props}
            togglePopup={this.togglePopup}
          />
        </div>
      </div>
    )
  }
}

export default Calender
